@charset "utf-8";
.product{
  width: 1200px;
  margin:0 auto;
}
.product_title{
  text-align: center;
}
.product_list{
  display: flex;
  justify-content: space-around;
  padding: 0px;
}
.product_list>li{
  width: 23%;
  list-style: none;
  overflow: hidden;
  text-align: left;
  font-weight: bold;
}
.product_list>li img{
  width: 100%;
}
.price{
  display: block;
  color: #f00;
  text-align: right;
}
